export default {
  mounted: function(){
    console.log("mixin mounted!!");
    this.scrollHeight = document.body.scrollHeight; //      100px  

  },
  updated: function(){ //   scroll  
    console.log("mixin updated!!");
    this.scrollHeight = document.body.scrollHeight; //- searchContainer.clientHeight;
  },
  data: function(){
    return {scrollHeight: 0};
  },
}



