export default {
  methods: {
    findYoutubeCardClass: function(path){
      let classKeyword = 'youtube-card'; //path  className 
      for (var i = 0; i < path.length; i++) {
        let currentVal = path[i]
        if(currentVal.className.indexOf(classKeyword) !== -1){ //   'youtube-card' class     
          return currentVal.getAttribute('value').split(' '); //     value    return
        }
      }
    },
  }
}



